.Navbar {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  position: fixed;
  z-index: 10;
  height: 20vw;

  top: 0;
  background: rgb(0, 0, 0);
}

.presentation-overlay {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  z-index: 0;
  animation-name: presentationOverlay;
  animation-duration: 1s;
  animation-delay: 2s;
  animation-duration: 5s;
  animation-fill-mode: forwards;
  opacity: 1;
  background-color: $color-primary;
}

.navbarFullHeight {
  height: 100vh;
}

.navbarBigHeight {
  height: 20vw;
}

.navbarSmallHeight {
  height: 17vw;
}

.menu-icon-container {
  width: 8vw;
  height: 8vw;
  position: absolute;
  z-index: 10;
  left: 6%;
  top: 4.5vw;
  opacity: 0;
}

.navbar-menu-icon {
  width: 100%;
  color: $color-white;
}

.menu-delay {
  animation-delay: 5.5s;
}

.navbar-logo-container {
  transition: 0.3s;
  will-change: width transform;
  position: relative;
  z-index: 21;
  margin-top: 1vw;
  transform: translateY(0vw);
  opacity: 0;
  animation-delay: 1s;
  animation-duration: 1s;
  & img {
    width: 100%;
  }
}
.logoWidthMenu {
  transform: translateY(28vw);
  width: 70vw;
}

.logoWidthBig {
  transform: translateY(0vw);

  width: 30vw;
}

.logoWidthSmall {
  width: 24vw;
  transform: translateY(-1vw);
}

.logoWidthPresentation {
  transform: translateY(25vh);
  width: 60vw;
  pointer-events: none;
}

.navbar-menu-container {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  transition: 0.3s;
  will-change: opacity;
  background-color: $color-primary;
  &__open {
    opacity: 1;
  }
  &__close {
    opacity: 0;
    pointer-events: none;
  }
}
.navbar-menu-list {
  padding-top: 48vw;
  list-style: none;
  margin: 0 auto;
}

.navbar-menu-links {
  font-size: 6vw;
  margin: 3vw 0;
  text-align: center;
  color: $color-white;
}
.navbar-media-container {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 11;
  bottom: 20vw;
  left: 27.5%;
  right: 27.5%;
  width: 45%;
}

.navbar-media-links {
  width: 10vw;
  height: 10vw;
  opacity: 0;
}

.media-image {
  filter: brightness(200%) grayscale(100%);
}

.media-ig {
  width: 100%;
}
.media-in {
  width: 85%;
}
.media-fb {
  width: 85%;
  margin-left: -2vw;
}

@keyframes presentationOverlay {
  0% {
    opacity: 1;
  }
  99% {
    opacity: 0;
  }
  100% {
    opacity: 0;

    display: none;
  }
}

@media screen and (min-width: 1099px) {
  .Navbar {
    align-items: center;
    justify-content: flex-start;
    padding-top: 0;
    height: 5vw;
  }

  .presentation-overlay {
  }

  .navbarBigHeight {
    height: 5vw;
  }

  .navbarSmallHeight {
    height: 3vw;
  }

  .navbar-menu-icon {
    display: none;
  }

  .navbar-logo-container {
    cursor: pointer;
    margin-left: 1vw;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0;
  }

  .logoWidthMenu {
    transform: translateY(0) translateX(0);
    width: 8vw;
  }

  .logoWidthBig {
    width: 8vw;
  }

  .logoWidthSmall {
    width: 5vw;
    transform: translateY(0);
  }

  .logoWidthPresentation {
    transform: translateY(30vh) translateX(38vw);
    width: 25vw;
  }

  .navbar-menu-container {
    width: 89%;
    height: auto;
    margin: -0.5vw 1% 0 auto;
    display: flex;
    justify-content: space-between;
    position: relative;
    opacity: 1;
    background-color: transparent;
    &__close {
      opacity: 1;
      pointer-events: all;
    }
  }
  .navbar-menu-list {
    padding: 0;
    margin: 0 0 0 0;

    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 45%;
  }

  .navbar-menu-links {
    margin: 0;
    font-size: 0.9vw;
    transition: 0.3s;
    will-change: color;
    cursor: pointer;
    animation-duration: 2s;
    opacity: 0;
    &:hover {
      color: $color-variation;
    }
    &__inactive {
      color: $color-white;
    }
    &__active {
      color: $color-variation;
    }
    &:nth-child(1) {
      animation-delay: 5.5s;
    }
    &:nth-child(2) {
      animation-delay: 5.6s;
    }
    &:nth-child(3) {
      animation-delay: 5.7s;
    }
    &:nth-child(4) {
      animation-delay: 5.8s;
    }
    &:nth-child(5) {
      animation-delay: 5.9s;
    }
    &:nth-child(6) {
      animation-delay: 6s;
    }
    &:nth-child(7) {
      animation-delay: 6.1s;
    }
  }

  .navbar-media-container {
    position: relative;
    right: 0;

    left: auto;
    bottom: auto;
    width: 9vw;
  }

  .navbar-media-links {
    width: 2.5vw;
    height: 2.5vw;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    will-change: opacity background-color;
    border-radius: 100vw;
    transition: 0.3s;

    background-color: $color-primary;
    &:nth-child(3) {
      animation-delay: 7s;
      animation-duration: 2s;
    }
    &:nth-child(2) {
      animation-delay: 7.1s;
      animation-duration: 2s;
    }
    &:nth-child(1) {
      animation-delay: 7.2s;
      animation-duration: 2s;
    }
    &:hover {
      background-color: $color-terciary;
      opacity: 1;
    }
    &:hover .media-image {
      filter: none;
    }
  }

  .media-image {
    filter: brightness(200%) grayscale(100%);
    transition: filter 0.3s ease;
    margin: 0;
  }

  .media-ig {
    width: 60%;
  }
  .media-in {
    width: 45%;
  }
  .media-fb {
    width: 50%;
  }
}
