.Hero {
}

.hero-container {
  width: 85%;
  margin: 43vw 0 0;
}

.hero-container img {
  width: 45vw;
  opacity: 0;
  animation-delay: 7.5s;
  animation-duration: 4s;
}

.hero-container h2 {
  width: 70%;
  margin: 1vw 0 4vw;
  font-weight: 400;
  opacity: 0;
  animation-delay: 8s;
  animation-duration: 4s;
}

.hero-button-container {
  opacity: 0;
  animation-delay: 8.5s;
  animation-duration: 4s;
}

@media screen and (min-width: 1099px) {
  .Hero {
    height: 100vh;
    width: 100%;
    background: rgb(0, 0, 0, 0);
  }

  .hero-container {
    width: 100%;
    margin: 10vw 0 0;
  }

  .hero-container h2 {
    width: 29%;
    margin: 0.5vw 0 1.5vw;
  }

  .hero-container img {
    width: 20vw;
  }
}
