.Nosotros {
  background-image: url(../../assets/nosotros-bg.jpeg);
  background-size: 270%;
  background-color: $color-primary;
  background-position: 0% center;
}

.nosotros-box {
  width: 80%;
  margin: 4vw 0;
}

@media screen and (min-width: 1099px) {
  .Nosotros {
    background-size: 100%;
    transition: 7s ease;
    will-change: background-size;

    padding-bottom: 10vw;
    height: auto;
    &:hover {
      background-size: 106%;
    }
  }

  .nosotros-box-container {
  }

  .nosotros-box {
    width: 48%;
    margin: 0;
  }
}
